.content-area ul li:after, .features-item .features-item-heading-text-decription h1:after, .features-item .features-item-heading-text-decription h2:after, .features-item .features-item-heading-text-decription h3:after, .features-item .features-item-heading-text-decription h4:after, .features-item .features-item-heading-text-decription h5:after, .features-item .features-item-heading-text-decription h6:after {
